<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show" @opened="opened">
  <el-form :model="form">
     <el-form-item label="一级分类" :label-width="formLabelWidth">
      <el-select v-model="form.first_cateid" @change="changeCate">
        <el-option label="--请选择--" value="" readonly></el-option>
        <!-- 动态数据获取 -->

        <el-option v-for="item in cateList" :key="item.id" :label="item.catename" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="二级分类" :label-width="formLabelWidth">
      <el-select v-model="form.second_cateid">
        <el-option label="--请选择--" value="" readonly></el-option>
        <!-- 动态数据获取 -->

        <el-option v-for="item in secondCate" :key="item.id" :label="item.catename" :value="item.id"></el-option>

      </el-select>
    </el-form-item>
    <el-form-item label="商品名称" :label-width="formLabelWidth">
      <el-input v-model="form.goodsname"></el-input>
    </el-form-item>
     <el-form-item label="价格" :label-width="formLabelWidth">
      <el-input v-model="form.price"></el-input>
    </el-form-item>
    <el-form-item label="市场价格" :label-width="formLabelWidth">
      <el-input v-model="form.market_price"></el-input>
    </el-form-item>
     <el-form-item label="图片" :label-width="formLabelWidth">
         <div class="box-img">
          <h3>+</h3>
          <img v-if="imageUrl" :src="imageUrl" alt="">
          <input type="file"  @change="changeImg">
        </div>
    </el-form-item>
     <el-form-item label="商品规格" :label-width="formLabelWidth">
      <el-select v-model="form.specsid" @change="changeSpecs">
        <el-option label="--请选择--" value="" readonly></el-option>
        <!-- 动态数据获取 -->
        <el-option v-for="item in specList" :key="item.id" :label="item.specsname" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
     <el-form-item label="规格属性" :label-width="formLabelWidth">
      <el-select v-model="form.specsattr">
        <el-option label="--请选择--" value="" readonly></el-option>
        <!-- 动态数据获取 -->

        <el-option v-for="(item,index) in secondSpecs" :key="index" :label="item" :value="item"></el-option>

      </el-select>
    </el-form-item>
    <el-form-item label="是否新品" :label-width="formLabelWidth">
       <el-switch
            v-model="form.isnew"
            active-color="blue"
            inactive-color="grey" :active-value="1" :inactive-value="2">
          </el-switch>
    </el-form-item>
    <el-form-item label="是否热卖" :label-width="formLabelWidth">
       <el-switch
            v-model="form.ishot"
            active-color="blue"
            inactive-color="grey" :active-value="1" :inactive-value="2">
          </el-switch>
    </el-form-item>
     <el-form-item label="商品状态" :label-width="formLabelWidth">
       <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey" :active-value="1" :inactive-value="2">
          </el-switch>
    </el-form-item>
     <el-form-item label="商品描述" :label-width="formLabelWidth">
       <div id="editor"></div>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
    <el-button type="primary" @click="update" v-else>修 改</el-button>
  </div>
</el-dialog>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import E from 'wangeditor';
import {requestGoodsAdd,requestGoodssOne,requestUpdateGoods} from  '../../../utils/request';
import { successAlert, warningAlert } from '../../../utils/alert';
export default {
  props:['info'],
  data(){
    return {
      value:null,
      formLabelWidth:'80px',
      // 显示的图片地址
      imageUrl:'',
      // 二级分类列表
      secondCate:'',
      // 规格属性列表
      secondSpecs:'',
      // 富文本的内容
      editor:'',
      form:{
        first_cateid:'',
        second_cateid:'',
        goodsname:'',
        price:'',
        market_price:"",
        img:'',
        description:"",
        specsid:'',
        specsattr:[],
        isnew:1,
        ishot:1,
        status:1
      }
    }
  },
  computed:{
    ...mapGetters({
      "cateList":"cate/list",
      "specList":"specs/list",
    })
  },
  methods:{
    ...mapActions({
      "requestCateList":"cate/listActions",
      "requestSpecsList":"specs/listActions",
      "requestGoodsList":"goods/listActions",
      "requestGoodsCount":"goods/countActions"
    }),
    // 获取二级分类
    changeCate(){

      // 先获取一级分类改变时取index
      var index = this.cateList.findIndex(item=>item.id==this.form.first_cateid);
      // 将二级分类放入secondCate
      this.secondCate = this.cateList[index].children;
      if(!index){
        this.form.second_cateid = '';
      }

    },
    // 获取图片信息
    changeImg(e){
      var file = e.target.files[0];
      // 处理文件大小
      if(file.size > 2*1024*1024){
        warningAlert('上传图片不能超过2M');
        return
      }

      // 处理文件后缀
      var ext = ['.jpg','.jpeg','.png','.gif'];
      var extName = file.name.slice(file.name.lastIndexOf('.'))
      if(!ext.some(item=>item==extName)){
        warningAlert('上传文件格式有误');
        return
      }

      //显示图片
      this.imageUrl = URL.createObjectURL(file)
      // 将文件信心存入img
      this.form.img = file;
    },
    // 获取规格属性
    changeSpecs(){

      var index =this.specList.findIndex(item=>item.id==this.form.specsid);
      // 获取规格属性
      this.secondSpecs = this.specList[index].attrs;
      if(!index){
         this.form.specsattr = [];
      }

    },
    //dialog打开后的回调
    opened(){
       this.editor = new E('#editor')
        this.editor.create()
        this.editor.txt.html(this.form.description);
    },
    cancel(){
      this.info.show = false;
      this.form = {
         first_cateid:'',
          second_cateid:'',
          goodsname:'',
          price:'',
          market_price:"",
          img:'',
          description:"",
          specsid:'',
          specsattr:[],
          isnew:1,
          ishot:1,
          status:1
      };
      this.imageUrl = '';
      this.secondCate ='';
      this.secondSpecs='';
    },
    confirm(){
      this.form.description = this.editor.txt.html()
      requestGoodsAdd(this.form).then(res=>{
        if(res.data.code == 200){
          successAlert(res.data.msg);
          this.cancel()
          this.requestGoodsCount()
          this.requestGoodsList(this.value)
        }else{
          warningAlert(res.data.msg)
        }
      })
    },
    getDetail(id){
      //获取商品详情
      requestGoodssOne({id:id}).then(res=>{
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$preImg+this.form.img;
      })

    },
    update(){
      this.form.description = this.editor.txt.html()
     requestUpdateGoods(this.form).then(res=>{
        if(res.data.code == 200){
          successAlert(res.data.msg);
          this.cancel()
          this.requestGoodsList(this.value)
        }else{
          warningAlert(res.data.msg)
        }
     })
    }
  },
  mounted(){
    //获取一级分类列表
    this.requestCateList();
    // 获取商品规格
    this.requestSpecsList();

  }
}
</script>

<style scoped lang="stylus">
.box-img {
  width 150px;
  height 150px;
  border: 1px dashed pink;
  position relative;
}
.box-img h3{
  width:100%;
  height 100%;
  line-height 150px;
  text-align center;
}
.box-img  img{
  width 100%;
  height 100%;
  position:absolute;
  left:0;
  top:0
}
.box-img input{
   width 100%;
    height 100%;
    position:absolute;
    left:0;
    top:0;
    opacity 0;
}
</style>



